import React from 'react';
import { useDispatch } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { red } from '@material-ui/core/colors';
import {
	IconButton,
	DataTable,
	DataColumnType,
} from '@nvdunginest/emis-mui';

import {
	AppDispatch,
	alertActions,
	alertMessage,
	loadingActions,
} from '../../../core';

import packageService, { PackageModel } from '../services/package';

import format from '../../../configs/format';

type Data = PackageModel & {
	action: JSX.Element,
}

const formatIsInternal = (value: Data) => {
	if (value.isInternal) {
		return (
			<>
				<FontAwesomeIcon
					icon="shield-alt"
					color={red[700]}
				/>
				Ni b
			</>
		)
	}
	else {
		return '';
	}
}

const formatBegin = (value: Data) => format.formatDate(new Date(value.begin), 'dd/MM/yyyy - hh:mm');
const formatEnd = (value: Data) => format.formatDate(new Date(value.end), 'dd/MM/yyyy - hh:mm');

const columns: DataColumnType<Data>[] = [
	{ id: 'code', label: 'M', numeric: false, sortable: true },
	{ id: 'name', label: 'Tn gi thu', numeric: false, sortable: false },
	{ id: 'isInternal', label: 'Loi', numeric: false, sortable: true, format: formatIsInternal },
	{ id: 'begin', label: 'M thu', numeric: false, sortable: true, format: formatBegin },
	{ id: 'end', label: 'ng thu', numeric: false, sortable: true, format: formatEnd },
	{ id: 'action', label: '', numeric: false, sortable: false },
];

export default function AuditorPackages(): JSX.Element {
	const { status }: never = useParams();
	const history = useHistory();
	const dispatch: AppDispatch = useDispatch();

	const [packages, setPackages] = React.useState<PackageModel[]>([]);
	const [title, setTitle] = React.useState<string>('Danh sch gi thu');

	const actionsButton = [
		<IconButton
			key={1}
			tooltip="Lm mi danh sch"
			text="Lm mi"
			icon="sync-alt"
			color="primary"
			onClick={() => { refresh(); }}
		/>,
	];

	const renderData = (data: PackageModel[]): Data[] => {
		return data.map(item => {
			const row: Data = {
				...item,
				action: (
					<>
						<IconButton
							tooltip="Xem chi tit gi thu"
							text="Xem"
							icon="info-circle"
							color="success"
							onClick={() => { history.push(`/bidding/auditor/package/${item.code}`) }}
						/>
					</>
				)
			}

			return row;
		});
	}

	const refresh = async () => {
		dispatch(loadingActions.show());
		try {
			setPackages(await packageService.getAuditorPackages(status));
		}
		catch {
			dispatch(alertActions.show(alertMessage.FETCH_FAILURE, 'error'));
		}
		finally {
			dispatch(loadingActions.hide());
		}
	}

	React.useEffect(() => {
		refresh();

		switch (status) {
			case 'new':
				setTitle('Danh sch gi thu mi');
				break;
			case 'opening':
				setTitle('Danh sch gi thu ang m');
				break;
			case 'closed':
				setTitle('Danh sch gi thu  ng');
				break;
			default:
				break;
		}

		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [status]);

	return (
		<DataTable
			title={title}
			columns={columns}
			data={renderData(packages)}
			actions={actionsButton}
			initialOrderBy="begin"
		/>
	)
}
